.navbar_main {
  background-color: #0050b3 !important;
  color: white;
}
.navbar-brand {
  color: white;
}
.container {
  width: 70% !important;
}
.nav-item a {
  color: white;
}
.dropdown-item {
  color: black !important;
}
.navbar__main {
  background-color: #0050b3 !important;
  color: white;
  height: 45px;
  border-bottom: 1px solid rgba(116, 116, 116, 0.466);
}
.navbar__main-logo {
  gap: 15px;
  display: flex;
}
.navbar__main-cont {
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  height: 100%;
}

.navbar__main-logo i {
  font-size: 20px !important;
}

.navbar__main-logo-right {
  gap: 10px;
  display: flex;
  align-items: center;
  text-align: center;
}
.navbar__main-logo-right p {
  margin-bottom: 0px;
}
@media screen and (max-width: 700px) {
  .container {
    width: 90% !important;
  }
}

@media screen and (max-width: 520px) {
  .container {
    width: 95% !important;
  }
  .navbar__main-logo {
    gap: 5px;
    display: flex;
  }
  .navbar__main-logo i {
    font-size: 18px !important;
  }
  .navbar__main-logo-right p{
    font-size: 14px;
  }
  .navbar__main-logo-right {
    gap: 5px;
  }
}
