.art1__main-img {
    width: 100%;
    height: 600px;
    overflow: hidden;
    display: flex;
    align-items: flex-start;
    position: relative;
}

.art1__main-img::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1;
}

.art1__main-img img {
    position: absolute;
    left: 0;
    top: -250px;
    width: 100%;
    height: auto;
    object-fit: cover;
    z-index: 0;
}

.art1__text {
    position: absolute;
    top: 30%;
    left: 40%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 44px;
    z-index: 2;
    padding: 10px;
    font-weight: 700;
    border-radius: 5px;
}

.article-content {
    padding: 20px;
    font-family: Arial, sans-serif;
    color: #333;
    line-height: 1.6;
}

.article-content h1 {
    font-size: 32px;
    margin-bottom: 20px;
    color: #000;
    font-weight: 600;
}

.article-content h2 {
    font-size: 28px;
    margin-top: 20px;
    font-weight: 600;
    margin-bottom: 15px;
    color: #222;
    width: 80%;
}

.article-content p {
    margin-bottom: 15px;
    width: 80%;
}
.article-content img{
    position: relative;
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
    object-fit: cover;
    z-index: 0;
    max-width: 650px;
}